import React, { useEffect, useState } from 'react'
import UserChat from './UserChat'
import axios from "../../utils/API"
import { FiSend } from 'react-icons/fi';
import { BiHome, BiLogOutCircle } from 'react-icons/bi';
import { useHistory } from 'react-router-dom';


const Massageuser = () => {

  const getCurrentDate=(separator='')=>{

let newDate = new Date()
let date = newDate.getDate();
let month = newDate.getMonth() + 1;
    let year = newDate.getFullYear();
    let time = newDate.toLocaleTimeString();

    return `${year}${separator}${month < 10 ? `0${month}` : `${month}`}${separator}${date} ${time}`
    
  }
  
  const iduser = localStorage.getItem("idusee");
  const idmustakdem = localStorage.getItem("idmustakdem");
  const [nameuser, setnameuser] = useState("");
  const [msguser, setmsguser] = useState("");
  const [admin, setadmin] = useState("");
  const [datamsg, setdatamsg] = useState([]);
      const history = useHistory();


  useEffect(  () => {
  
    const fetchdata =async () => {
const usermsg =await axios.get(`msg/${idmustakdem}`)
 setdatamsg(usermsg.data[0].msg)
    const userone =await  axios.post(`users/${iduser}`);
    // console.log(userone.data.msg)

    setnameuser(userone.data.username);
    setadmin(userone.data.role);

    
    
    }

    fetchdata()
   
    
  });

  
  

  

  const sendmsg = async () => {
   // event.preventdefault()
      if (msguser==="") {
        alert("حقل الرسالة فارغ يرجى ملئ الحقل لارسال ")
      }

      else {
         

        setmsguser("")
      await axios.post("msg/", {
          iduser:idmustakdem,
          username:nameuser,
          msg:msguser,
          date:getCurrentDate("-"),
          admin: admin === "مدير نظام" ? true : false,
         red: false,
          idmsg:iduser
        })
      }
     



    };
    
  const redmsg = async () => {
   await  axios.put("msg", {iduser:idmustakdem})
  }
   const loguotuser = async () => {
          localStorage.removeItem("LOGINUSER");
        localStorage.removeItem("idusee");

        history.push("/login", { from: "Massageuser" });

          
  }

  const gotohome = async () => {
     
    if (admin === "مدير نظام") {
       history.push("/usersmange", { from: "Massageuser" });
    }

    else {
        history.push("/Mustakdem", { from: "Massageuser" });
    }
       

          
  }

  
  

  
  return (
      <div>
   
       
      <div className="Massageuser">
       
          <div className="users_header">
        <h5>{nameuser}</h5>
          <div className="dashboard_header_div">
            <h5 style={{ cursor: "pointer" }} onClick={gotohome} className="link_header_dashboard ml-2">الرئيسية<BiHome className="mr-1" size={25} /></h5>
            
            <h5 style={{ cursor: "pointer" }} onClick={loguotuser} className="link_header_dashboard">تسجيل الخروج<BiLogOutCircle className="mr-1" size={25} /></h5>
            
            
            
      </div>
      </div>
            <div className="container">
                <div className="MainBoxMsg">
                    <div className="MsgContainer">
                        {datamsg.map((res,i) => 
                            
                            res.admin===false? 
                        
                            <div key={i} style={{ display: "flex", justifyContent: "flex-start" }}>
                                    <UserChat classdiv={"BoxMsgUser"} dtxt={"rtr"} arrwtime={true}
                                        nameuser={ res.username} msguser={res.msg} datemsg={res.date} />
                                </div> :
                                  <div  key={i} style={{ display: "flex", justifyContent: "flex-end" }}>
                                    <UserChat classdiv={"BoxMsgAdmin"} dtxt={"ltr"} arrwtime={false}
                                        nameuser={ res.username} msguser={res.msg} datemsg={res.date} />
                                </div> 
                                


                            
                        )}
                       
                      
                      
                       
            
            </div>
                 
            <form >
                 <div className="BoxMsgSend">
                   <textarea placeholder="يرجى كتابة الرسالة هنا"   onChange={(e)=>setmsguser(e.target.value)} value={msguser}  id="w3review" className="inoutmsg" name="w3review" rows="2" cols="50"/>

                        <button style={{height:50,display:"flex",alignItems:"center",background:"#FF5722",fontWeight:700}} type="button" onClick={sendmsg} className="btn btn-primary btn-mobail mr-3" >
                  ارسال<FiSend className="mr-2" style={{ color: "#fff" }} size={30}/>
              </button>

              <button onClick={redmsg}>red</button>
                     </div>
            </form>
            
                   

                </div>
        </div>
        
      </div>
      
      </div>
    )
}

export default Massageuser
