import React from 'react'

const UserChat = ({ classdiv, dtxt, arrwtime,nameuser,msguser,datemsg }) => {
    
    return (
        <div className={classdiv}>
                        <h5 style={{direction:dtxt}}>{nameuser} : </h5>
            <span style={{wordBreak:"break-word"}} className="mr-1">{msguser}</span>
            {arrwtime ? <span style={{ position: "absolute", top: 5, left: 5 }}>{datemsg}</span>:<span style={{ position: "absolute", top: 5,right:5}}>{datemsg}</span>}
            
                    </div>
    )
}

export default UserChat
