import React, { useEffect, useState } from 'react'
import {  useHistory } from 'react-router-dom';
import "./style.css";
import { BiLogOutCircle ,BiMailSend} from 'react-icons/bi';
import { FaUsersCog } from 'react-icons/fa';
import axios from "../../utils/API";


const NavDashBoard = () => {
  const iduser = localStorage.getItem("idusee");
const [userid, setuserid] = useState("")
    console.log(userid)
      const history = useHistory();

    const loguotuser = async () => {
          
        let  answer = window.confirm("هل انت متاكد من  تسجيل الخروج?");
if (answer) {
   localStorage.removeItem("LOGINUSER");
        localStorage.removeItem("idusee");

        history.push("/login", { from: "NavDashBoard" });

}
else {
    
        }
        
         

          
    }
    
    const settinguser = () => {
                history.push("/usersmange", { from: "NavDashBoard" });

    }

    
    const gotmsg = async () => {
   // history.push("/Massageuser", { from: "Massageuser" });
 await axios.put("msg/", {iduser:userid})
  

    }
    
    const [datamsg, setdatamsg] = useState([]);

    useEffect( () => {
   setuserid(iduser)
        const fetchdata = async () => {
       
      const usermsg =await axios.get(`msg/`)
     //setdatamsg(usermsg.msg)
        usermsg.data.map(res=>setdatamsg(res.msg))
       

    }
    fetchdata()
   
    
    });
    
    

 const msgttl = datamsg.filter(res => res.red === false && res.idmsg !== iduser ).map(res => res)
  
    
    return (
        <div className="dashboard">
            <div className="dashboard_header">
                <div onClick={settinguser} style={{cursor:"pointer"}}  className="dashboard_header_div link_header_dashboard">

              <FaUsersCog className="ml-2" size={30}/> ادارة المستخدمين                   
                </div>
   <h5 style={{ cursor: "pointer",position:"relative" }} onClick={gotmsg} className="link_header_dashboard ml-2">الرسائل<BiMailSend className="mr-1" size={25} /> <span className="msglengh">{msgttl.length}</span></h5>
                <div className="dashboard_header_div">
                    <div style={{ cursor: "pointer" }} onClick={loguotuser} className="link_header_dashboard">تسجيل الخروج<BiLogOutCircle className="mr-2" size={30}/></div>
                 </div>
                
               

            </div>
        </div>
    )
}

export default NavDashBoard
